.slider {
    width: 100%;
    -webkit-appearance: none;
    appearance: none;
    /* Override default CSS styles */
    height: 60%;
    /* Specified height */
    border-radius: 5px;
    /* Grey background */
    outline: none;
    /* Remove outline */
    
}

.sliderBox {
    display: block;
    display: flex;
    align-items: center;
}

.brightness-slider {
    background-image: linear-gradient(to right, #747474, #E8E8E8);
}

.color-slider {
    background-image: linear-gradient(to right, #EAF9F9, #F5D141);
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    /* Override default look */
    appearance: none;
    width: 22px;
    /* Set a specific slider handle width */
    height: 22px;
    border: none;
    /* Slider handle height */
    border-radius: 50%;
    background: #F39C12;
    cursor: pointer;
    /* Green background */
}

.slider::-webkit-slider-thumb:active{
    transform: scale(2);
}

.slider::-moz-range-thumb {
    width: 18px;
    /* Set a specific slider handle width */
    height: 18px;
    border: none;
    /* Slider handle height */
    border-radius: 50%;
    background: #F39C12;
    cursor: pointer;
    /* Green background */
}

.brightness-slider::-moz-range-thumb {
    background: #F1C40F;
}