@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: normal;
  src: url("./Montserrat-Medium.ttf")format('truetype');
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  box-sizing: border-box;
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-style: normal;
  height: 100%;
}

#root {
  background-color: lightgrey;
}


a {
  color: inherit;
}