.statusScreen {
    display: flex;
    align-items: center;
    justify-content: center;
}

.center {
    height: 122px;
    width: 122px;
    text-align: center;
}

.powerbutton {
    width: 100%;
    cursor: pointer;
    background-color: #c8c8c8;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: none;
    box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.322);
    -webkit-tap-highlight-color: transparent;
}

.powerbutton:active {
    background-color: #dddbdb;
    transform: scale(1.1);
}

.powerbutton:focus{
    outline:0;
}


.off {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
}