.AppContainer{
  position: relative;
  font-size: 12px;
  height: 100%;
  background-color: white;
  margin: 0 auto;
 
}

@media only screen and (min-width: 768px) {
  .AppContainer {
      margin: 0 auto;
      max-width: 56.25vh;
      border-radius: 20px;
      overflow: hidden;
      box-shadow: 10px 20px 25px 1px rgba(0, 0, 0, 0.527);
  }
}